<template>
  <section class="profile-wrapper">
    <div class="line-eq-wrapper">
      <div class="kaede-image">
      </div>
      <table>
        <tbody>
          <tr v-for="(item, i) in items" :key="i">
            <th>{{ item.key }}</th>
            <td v-if="item.url" class="last-td">
              <a :href="item.url" target="_blank" rel="noopener noreferrer">
                {{ item.value }}
              </a>
            </td>
            <td v-else class="last-td">
              {{ item.value }}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Profile',
  data () {
    return {
      items: [
        {title: 'Name', value: '広田楓(ひろたかえで)', url: ''},
        {title: 'University', value: '公立はこだて未来大学 情報システム科学部 複雑系コース', url: ''},
        {title: 'E-mail', value: 'b1017222@fun.ac.jp', url: ''},
        {title: 'GitHub', value: 'b1017222', url: 'https://github.com/b1017222'}
      ]
    }
  }
}
</script>

<style scoped>
  table {
    text-align: left;
  }
  th {
    padding-right: 20px;
    font-weight: normal;
    color: saddlebrown;
  }
  td {
    font-size: 20px;
    padding: 5px 8px 5px 0;
  }
  a {
  text-decoration: none;
}

@media (max-width: 670px) {
    td {
      display: block;
      font-size: 16px;
      width: 100%;
      padding: 5px 0;
      border-bottom: none;
    }
}

</style>



